#contactpage{
    min-height: 100vh-4rem;
    width: 100vw;
    max-width: 60rem;
    margin: 0 auto ;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.contactpagetitle {
  font-size: 3rem;
  margin:2rem 0 1.5rem 0 ;

}

.clientdescr {
  font-weight: 300;
  font-size: 1.5rem;
  word-spacing: 0.2rem;
}

.clientimgs{
    margin: 1rem 0;
    display: flex;
    flex-wrap: wrap;
}

.clientimg {
  object-fit: cover;
  width: 23%;
  min-width: 9rem;
  margin: auto ;
  padding: 0.25rem;
}
#clients{
    padding: 2rem;
}


.contactdesc {
  padding: 1rem;
  font-size: 1.2rem;
  font-weight: 200;
}

.contactform {
  margin: 1rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 90vw;
  max-width: 60rem;
}

.name, .email, .msg{
 font-size: medium;
 width: 100%;
 max-width: 40rem;
 margin: 0.5rem;
 padding: 0.5rem 1rem;
 color: white;
 border: none;
 border-radius: 1 rem;
 background: rgb(40,40,40);
}

.submitbtn {
  background: white;
  border: none;
  padding: 0.75rem 3.5rem;
  border-radius: 0.5rem;
  margin: 2rem;
}

.links{
    display: flex;
    flex-wrap: wrap;
}
.link{
    object-fit: cover;
    height: 3rem;
    width: 3rem;
    margin: 0 0.75rem;
}